<template>
  <full-height-layout>
    <div slot="layout_title">客戶</div>

    <member-view-top slot="layout_inner_head"></member-view-top>

    <!-- 表格主體 -->
    <transition v-if="isLoading" slot="layout_inner_body" name="fade">
      <loading></loading>
    </transition>

    <member-view-body
      slot="layout_inner_body"

      :members="memberList"

      @option_Father="option"

      v-if="!isLoading"
    ></member-view-body>

    <!-- 其它插件 -->
    <div slot="layout_extra">

      <trash @trashItem="_trash"></trash>

    </div>
  </full-height-layout>
</template>

<script>
import view from '@/common/es/view'
import config from '@/common/config'
import tool from '@/common/es/tool'
import FullHeightLayout from '@/components/Layout/FullHeightLayout.vue'

import conn from '@/service/conn'
import MemberViewTop from './Top/MemberViewTop.vue'
import MemberViewBody from './Body/MemberViewBody.vue'
import Loading from '@/components/Shimmer/Loading.vue'
import Trash from '@/components/Modal/Trash.vue'

export default {
  components: { FullHeightLayout, MemberViewTop, MemberViewBody, Loading, Trash },
  name: 'MemberView',
  data() {
    return {
      memberList: [],
      activeId: null,
      isLoading: true
    }
  },
  mounted() {

    this.loadData()
  },
  methods: {
    loadData() {
      setTimeout(() => {
        conn.member.getList_Member({}, true).then(res => {
          
          this.memberList = res
          this.isLoading = false

        }).catch(err => {
          tool.showToast('網絡連接失敗。')
        })
      }, config.delayTime)
    },
    option(id, option) {
      this.activeId = id

      if (option == 'update') {
       this.$router.push({
          path: '/home/member_update',
          query: {
            memberId: id
          }
        })
      } else if (option == 'trash') {
        tool.showModal('trashModal')
      }
    },
    // 數據操作
    _trash() {
      const _this = this
      tool.closeModal('trashModal')

      if ((this.activeId != null) && (this.activeId > 0)) {

        conn.member.trash(this.activeId).then(res => {
          try {
            const id = res['id']
            _this.isLoading = true
            _this.loadData()

          } catch(err) {

          }
        })

      }
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


